













































































import {
  computed, defineComponent, PropType,
} from '@vue/composition-api';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import Icon from '@/app/editor/client/view/icon';
import useI18N from '@/app/shared/client/logic/i18n';
import { LogoData, LogoType } from '@/app/shared/client/type/logo';
import getImageUrl from '@/app/shared/client/helper/image-url';
import {
  getImageInUploadingState,
} from '@/app/website/client/logic/image-uploading';
import { Store } from 'vuex';
import { State } from '@/app/editor/client/store/state';

const LogoContentEditor = defineComponent({
  components: {
    PlusIcon: Icon.PlusIcon,
  },
  props: {
    tileId: {
      required: true,
      type: String,
    },
    label: {
      required: true,
      type: String,
    },
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<LogoData>,
    },
  },
  setup(props, { root }) {
    const store = root.$store as Store<State>;
    const i18n = useI18N(store);

    const isTextLogo = computed(() => props.value?.type === LogoType.TEXT);
    const isImageLogo = computed(() => props.value?.type === LogoType.IMAGE);

    const hasLogo = computed(() => {
      if (isTextLogo.value) {
        return isNotNullOrEmpty(props.value?.text);
      }

      if (isImageLogo.value) {
        return props.value?.image !== undefined;
      }

      return false;
    });
    const currentUploadingLogoImage = computed(() => {
      const { imageListInUploadingState } = store.state;
      return getImageInUploadingState(imageListInUploadingState, props.tileId, 'logo-image');
    });
    const isUploadingLogoImage = computed(() => currentUploadingLogoImage.value !== undefined);

    const logoText = computed(() => props.value?.text);
    const logoImage = computed(() => {
      if (currentUploadingLogoImage.value !== undefined) {
        return currentUploadingLogoImage.value.rawImage;
      }

      const imageData = props.value?.image;
      const bucket = imageData?.bucket;
      const currentImage = imageData?.set?.['default-200x200']?.url;
      return getImageUrl(currentImage, bucket, root.$store.state.context.bucketUrls);
    });

    return {
      i18n,
      isTextLogo,
      isImageLogo,
      hasLogo,
      isUploadingLogoImage,
      logoText,
      logoImage,
    };
  },
});

export default LogoContentEditor;
